.aboutWrapper {
  padding: 84px 0;
  margin: 0 auto;
}
.aboutText {
  font-size: 11px;
  text-transform: uppercase;
  margin-bottom: 20px;
  position: relative;
  margin-left: 80px;
}
.aboutText::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateX(-80px);
  width: 60px;
  height: 1px;
  background-color: var(--primary-font-color-303030);
}
.aboutTitle {
  font-weight: 700;
  font-size: 28px;
  letter-spacing: 0.05em;
  font-family: var(--secondary-font-Raleway);
  margin-bottom: 20px;
  width: 306px;
}
.aboutTextTwo {
  font-weight: 400;
  font-size: 16px;
  line-height: 162%;
  letter-spacing: 0.02em;
  color: var(--secondary-font-color-555555);
  margin-bottom: 20px;
}

.aboutDescr {
  font-weight: 400;
  font-size: 14px;
  line-height: 186%;
  letter-spacing: 0.02em;
  color: var(--secondary-font-color-555555);
  margin-bottom: 40px;
}
.aboutBtn {
  padding: 12px 20px;
  border-radius: 25px;
  border: 1px solid var(--border-color-FF6C00);
  font-size: 12px;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  background-color: inherit;
}
.aboutBtn:hover,
.aboutBtn:focus {
  color: var(--white-color-FFFFFF);
  background-color: var(--border-color-FF6C00);
  transition-duration: var(--my-transition-duration);
  transition-timing-function: var(--my-transition-timing-function);
  transition-delay: var(--my-transition-delay);
}
.tabletList {
  display: none;
}
@media (min-width: 768px) {
  .about {
    max-width: 768px;
    margin: 0 auto;
  }
  .container {
    margin: 0 auto;
    min-width: 768px;
  }
  .aboutWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 30px;
    padding-top: 77px;
    padding-bottom: 123px;
  }
  .tabletList {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  .tabletImg1 {
    min-width: 330px;
  }
  .tabletImg2 {
    display: none;
  }
  .aboutTextTwo {
    font-size: 18px;
    line-height: 167%;
    letter-spacing: 0.02em;
  }
  .aboutDescr {
    margin-bottom: 77px;
  }
}

@media (min-width: 1600px) {
  .about {
    max-width: 1600px;
    margin: 0 auto;
  }
  .tabletImg1 {
    display: none;
  }
  .tabletList {
    flex-direction: row;
  }
  .tabletImg2 {
    display: block;
    min-width: 270px;
  }
  .aboutWrapper {
    padding: 120px 0;
  }
  .aboutTitle {
    font-weight: 700;
    font-size: 42px;
    letter-spacing: 0.05em;
    width: 470px;
  }
  .aboutMobile {
    width: 100%;
  }
}
